<template>
  <ProgressCircular v-if="!videoID" />
  <div v-else class="mt-5">
    <lite-youtube
      class="rounded-lg elevation-10"
      :videoid="videoID"
      :params="`controls=1${startPoint ? `&start=${startPoint}` : ''}`"
      title="Keynote (Google I/O '18)"
      ></lite-youtube>

    <!-- <iframe
      class="rounded-lg"
      :width="isMobile() ? 300 : 500"
      :height="isMobile() ? 200: 350"
      :src="embedUrl"
      frameborder="0"
      scrolling="no"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
      allowfullscreen
    ></iframe> -->
  </div>
</template>

<script>
export default {
  name: 'YoutubeIframe',

  data () {
    return {
      videoID: null,
      startPoint: null
    };
  },

  props: {
    link: {
      type: String,
      required: true
    }
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: function () {
      this.videoID = this.link.split('youtu.be/')[1].split('?t=')[0];
      this.startPoint = this.link.includes('?t=') ? this.link.split('?t=')[1] : null;
    },
    isMobile () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        case 'md':
          return false;
        case 'lg':
          return false;
        case 'xl':
          return false;
      }
    }
  }
};
</script>
