<template>
  <v-app class="fade-in">
    <v-main class="blue-grey--text text--darken-3">
      <v-container fluid class="py-16">
        <div :class="`text-center font-${ isMobile() ? '42' : '60' } font-weight-bold mt-10 mb-4`">劉佳家 Mia Liu</div>

        <v-img
          :class="`mx-auto ma-16 elevation-10 ${isMobile() ? 'rounded-lg' : 'rounded-pill'}`"
          :max-width="isMobile() ? 250 : 350"
          :max-height="isMobile() ? 250 : 350"
          src="https://raw.githubusercontent.com/jia-jia-liu/jia-jia-liu.github.io/main/api/profile.jpg"
        ></v-img>

        <ProgressCircular v-if="!aboutMe" />
        <div v-else class="ma-10 text-left">
          <template v-for="line in aboutMe.split('\n')">
            <span :class="`font-${ isMobile() ? 10 : 16 }`" :key="line.id">{{ line }}</span>
            <br :key="line.id">
          </template>
        </div>

        <Divider class="my-16" />

        <Title text="作品集．Collection" />

        <ProgressCircular v-if="!collection.peopo" />
        <div v-else v-for="category in Object.keys(collection.peopo)" :key="category.id">
          <SubTitle :text="`【 ${category} 】`" />

          <v-row align="center" justify="center">
            <v-col class="mb-8" v-for="collection in collection.peopo[category]" :key="collection.id" align="center" justify="center" :cols="isMobile() ? 12 : 6">
              <span :class="`font-${ isMobile() ? 10 : 16 } blue-grey--text text--darken-1`">{{ collection.title }}</span>
              <PeopoIframe :link="collection.url" />
            </v-col>
          </v-row>
        </div>

        <ProgressCircular v-if="!collection.youtube" />
        <div v-else v-for="category in Object.keys(collection.youtube)" :key="category.id">
          <SubTitle :text="`【 ${category} 】`" />

          <v-row align="center" justify="center">
            <v-col class="mb-8" v-for="collection in collection.youtube[category]" :key="collection.id" align="center" justify="center" :cols="isMobile() ? 12 : 6">
              <span :class="`font-${ isMobile() ? 10 : 16 } blue-grey--text text--darken-1`">{{ collection.title }}</span>
              <YoutubeIframe :link="collection.url" />
            </v-col>
          </v-row>
        </div>

        <Divider class="my-16" />
        
        <Title text="聯絡．Contact" />
        <v-row class="my-10">
          <v-col v-for="link in links" :key="link.id"><CardIcon :name="link.name" :icon="link.icon" :link="link.link" /></v-col>
        </v-row>
      </v-container>

      <v-footer padless>
        <v-card class="flex text-center" flat tile>
          <v-divider class="mt-16" />
          <div class="font-10 text-center ma-4">Mia Liu © {{ new Date().getFullYear() }}</div>
        </v-card>
      </v-footer>
    </v-main>
  </v-app>
</template>

<script>
import api from '@/lib/api';

export default {
  name: 'App',

  data () {
    return {
      aboutMe: null,
      collection: {
        peopo: null,
        youtube: null
      },
      links: null
    };
  },

  created () {
    this.$vuetify.theme.dark = false;
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.aboutMe = (await api.get('about')).me;
      this.collection.peopo = (await api.get('collection')).peopo;
      this.collection.youtube = (await api.get('collection')).youtube;
      this.links = await api.get('link');
    },
    isMobile () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        case 'md':
          return false;
        case 'lg':
          return false;
        case 'xl':
          return false;
      }
    }
  }
};
</script>

