<template>
  <v-card class="mx-auto" :width="isMobile() ? '100%' : '80%'" :href="link" target="_blank" color="blue-grey darken-2" dark>
    <v-card-title class="justify-center align-center"><v-icon left>{{ icon }}</v-icon><div class="font-18">{{ name }}</div></v-card-title>
  </v-card>
</template>

<script>
export default {
  name: 'CardIcon',

  props: {
    name: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      required: true
    },
    link: {
      type: String,
      required: true
    }
  },

  methods: {
    isMobile () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        case 'md':
          return true;
        case 'lg':
          return false;
        case 'xl':
          return false;
      }
    }
  }
};
</script>
