<template>
  <div :class="`text-center font-${ isMobile() ? '24' : '36' } font-weight-bold mt-10 mb-4`">{{ text }}</div>
</template>

<script>
export default {
  name: 'Title',

  props: {
    text: {
      type: String,
      required: true
    }
  },

  methods: {
    isMobile () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        case 'md':
          return false;
        case 'lg':
          return false;
        case 'xl':
          return false;
      }
    }
  }
};
</script>
