import Vue from 'vue';
import vuetify from './plugins/vuetify';

import App from './App.vue';

// Customized Components
import CardIcon from '@/components/CardIcon';
import Divider from '@/components/Divider';
import PeopoIframe from '@/components/PeopoIframe';
import ProgressCircular from '@/components/ProgressCircular';
import SubTitle from '@/components/SubTitle';
import Title from '@/components/Title';
import YoutubeIframe from '@/components/YoutubeIframe';

Vue.config.productionTip = false;
Vue.config.devtools = false;

Vue.component('CardIcon', CardIcon);
Vue.component('Divider', Divider);
Vue.component('PeopoIframe', PeopoIframe);
Vue.component('ProgressCircular', ProgressCircular);
Vue.component('SubTitle', SubTitle);
Vue.component('Title', Title);
Vue.component('YoutubeIframe', YoutubeIframe);

new Vue({
  vuetify,
  render: h => h(App)
}).$mount('#app')
