<template>
  <ProgressCircular v-if="!embedUrl" />
  <div v-else class="mt-5">
    <iframe
      class="rounded-lg"
      width="100%"
      :height="isMobile() ? 230: 350"
      :src="embedUrl"
      frameborder="0"
      scrolling="no"
      allowfullscreen
    ></iframe>
  </div>
</template>

<script>
export default {
  name: 'PeopoIframe',

  data () {
    return {
      embedUrl: null
    };
  },

  props: {
    link: {
      type: String,
      required: true
    }
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: function () {
      this.embedUrl = this.link;
    },
    isMobile () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        case 'md':
          return false;
        case 'lg':
          return false;
        case 'xl':
          return false;
      }
    }
  }
};
</script>
